.welcome {
  width: 1920px;
  padding: 155px 725px 280px 475px;
  margin: 0;
  margin-top: 80px;
  background-image: url("../../img/welcome.jpg");
  background-color: $color-black-pearl;
background-repeat: no-repeat;
height: 100%;

  @media screen and (max-width: 1023px) {
    width: 770px;
    padding: 58px 50px 205px 50px;
    background-image: url("../../img/welcome-tablet.jpg");
    margin-top: -30px;

}

@media screen and (max-width: 767px) {
width: 320px;
padding: 80px 25px 190px 25px;
background-image: url("../../img/welcome-mobile.jpg");
}
}


.welcome__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  font-weight: 700;
  line-height: 94px;
  margin-bottom: 38px;
  text-transform: uppercase;
  letter-spacing: 5px;

  @media screen and (max-width: 1023px) {
    font-size: 64px;
    line-height: 75px;
margin-bottom: 0;
}
@media screen and (max-width: 767px) {
  font-size: 36px;
  line-height: 42px;
}
}

.welcome__text {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 34px;
  margin-bottom: 45px;

  @media screen and (max-width: 1023px) {
    margin: 0;
    margin-bottom: 57px;
}

@media screen and (max-width: 767px) {
  font-size: 14px;
  line-height: 22px;
  margin-bottom: 50px;
}
}
.welcome__button {
  display: block;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 21px;
  text-align: center;
  width: 410px;
  padding: 16px;
  letter-spacing: 1px;
  color: $color-alice-blue;
  background-color: $color-deep-sky-blue;
  text-transform: uppercase;
  border-radius: 3px;

  @media screen and (max-width: 1023px) {
    margin-left: 45px;


}

@media screen and (max-width: 767px) {
display: none;
}
}

.welcome__mobile {
display: none;

@media screen and (max-width: 767px) {
  display: block;
  width: 270px;
  padding: 10px 30px;
  font-size: 14px;
  line-height: 17px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  text-align: center;
  color: $color-alice-blue;
  background-color: $color-deep-sky-blue;
  text-transform: uppercase;
}
}
